.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* END CRA STUFF */

body {
  font-family: "Metropolis", 'Open Sans', sans-serif;
}

.App {
  color: #fff;
}

.isitweeka {
  padding-top: 15em;
}

.isitweeka h1 {
  font-size: 10em;
  margin-top: 0;
  margin-bottom: 0;
}
.isitweeka h2 {
  font-size: 4em;
}
.isitweeka h3 {
  font-size: 3.5em;
}

.isitweeka h4 {
  font-size: 2.5em;
}

.isitweeka a {
  color: #00B0FF;
}
